@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gravitas+One&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Mulish', sans-serif}.navbar{background-color:#fff !important}.navbar a{font-size:1.3rem;text-transform:capitalize}a.navbar-brand{font-size:30px;font-family:'Gravitas One', cursive}.itsme_logo{height:17%;width:7%}.menu_active{font-weight:bold;border-bottom:1px solid #565387}a:hover{color:red !important}.contact_div{width:100%;height:75vh}footer p{padding:0;margin:0}.card-img-top{width:300px;height:200px}@media (max-width: 991px){#header{height:80vh;text-align:center}#header .header-img{text-align:center}#header .header-img img{width:60%}#header .header-img img{width:60%}.heading_text{font-size:1.5rem !important}.brand-name{font-size:2.5rem !important}.menu_active{font-weight:bold;border-bottom:none}}@media (max-width: 768px){#header h1{font-size:15px}#header h2{font-size:15px}}#header{width:100%;height:70vh;margin-top:65px}#header h1{margin:0 0 10px 0;font-size:48px;font-weight:700;color:black}#header .brand-name{color:red}#header h2{color:#484848;font-size:24px}#header .btn-get-started{font-weight:900;font-size:16px;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:red;border:2px solid red;text-decoration:none}#header .btn-get-started:hover{color:#fff !important;background-color:red}.header_img{text-align:right}#header .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}

